:root {
  --white: #ffffff;
  --primaryColor: #e60000;
  --darkGray: #707070;
  --lightGray: #fafafa;
  --lightRed: #f54845;
  --darkRed: #ce3835;
  --white: #ffff;
  --darkBlue: #162e4a;
  --extraDarkGray: #484848;
  --extraLightGray: #fafafa;
  --gray: #707070;
  --lightBlack:#142645;
}

body {
  padding: 0;
  margin: 0;
  font-family: "Cairo";
  color: var(--darkBlue);
  overflow: hidden;
}

[class^=icon-],
[class*=" icon-"] {
  display: inline-block;
  font-size: 20px;
}

.break {
  font-size: 34px;
  color: darkgrey;
  margin: -2px 18px;
}

.required {
  color: #e60000;
  vertical-align: middle;
}

.page-divider {
  margin: 20px 0px;
}

.btn {
  border-radius: 12px;
  font: normal normal normal bold 15px/15px Cairo;
}

.btn-basic {
  background-color: var(--primaryColor);
  border-color: var(--primaryColor);
  box-shadow: 6px 5px 11px #e600004f;
}

.btn-basic:enabled:hover {
  background: var(--primaryColor);
  border-color: var(--primaryColor);
}

.btn-basic:focus {
  box-shadow: 6px 5px 11px #e600004f;
  color: var(--white) !important;
}

.btn-light,
.btn-light:enabled:hover,
.btn-light:focus {
  background-color: var(--white);
  color: var(--primaryColor);
  border: 0;
  box-shadow: unset;
}

.layout-margin {
  margin-left: 30px;
  margin-right: 30px;
}

.head-icon {
  color: #142645b3;
  padding-right: 5px;
  font-size: 17px;
  padding-top: 1px;
  font-weight: bold !important;
}

.head-font {
  color: var(--lightBlack);
  font: normal normal normal bold 17px/17px Cairo;
  letter-spacing: 1.25px;
}

.white-button {
  font: normal normal normal bold 17px/17px Cairo;
  color: var(--primaryColor);
  padding-bottom: 10px;
  padding: 5px !important;
  border-radius: 10px;
}

::-webkit-scrollbar {
  width: 6px !important;
}

::-webkit-scrollbar-track {
  background: var(--white);
}

::-webkit-scrollbar-thumb {
  background: var(--primaryColor);
  border-radius: 50px;
}

app-sidebar ::-webkit-scrollbar {
  width: 4px !important;
}
app-sidebar ::-webkit-scrollbar-track {
  background: var(--white);
}
app-sidebar ::-webkit-scrollbar-thumb {
  background: var(--primaryColor);
  border-radius: 50px;
}

.red-button {
  background-color: var(--primaryColor);
  border: none;
  font: normal normal normal bold 17px/17px Cairo;
  padding-left: 10px;
  border-radius: 12px;
  box-shadow: 6px 5px 11px #e123204d;
}

.text {
  color: var(--lightBlack);
  font: normal normal normal bold 17px/17px Cairo;
  letter-spacing: 1.25px;
}

.placeholder {
  color: var(--extraDarkGray);
  font: normal normal normal 12px/12px DIN Next LT Arabic;
  font-weight: bold;
  padding-left: 15px;
}

.modal .tableDrop {
  width: 25vw !important;
}

hr {
